:root {
    --color-white: #f4f4f4;
    --color-grey-dark: #242A38;
    --color-grey-light: #4E586E;
    --color-gradient: linear-gradient(#F78361, #F54B64);
    --color-red: #F54B64;
    --color-orange: #F78361;
}

* {
    padding: 0;
    margin: 0;
}

h1 {
    color: var(--color-grey-dark);
}

h2 {
    color: var(--color-grey-dark);
    font-weight: 600;
}

.App {
    align-items: center;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1vh;
    margin-bottom: 1vh;
    width: 480px;
    height: 98vh;
    background-color: var(--color-white);
    border-radius: 8px;
}

@media only screen and (max-width: 480px) {
    .App {
        width: 96%;
        max-width: 100%;
        overflow: hidden;
    }
}

.divider {
    border-radius: 10px;
    border-top: 1px solid var(--color-grey-light);
}

/*#region Navbar*/
.nav {
    padding: 10px;
    padding-bottom: 0px;
}

.nav--div {
    position: relative;
}

.nav--arrow {
    width: 40px;
    height: 40px;
    color: var(--color-grey-dark);
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    margin: auto 0;
}

.nav--arrow:hover {
    cursor: pointer;
}

.nav--icon {
    width: 50px;
    height: 50px;
    color: var(--color-grey-dark);

}
/*#endregion*/

/*#region Options*/
.options--div {
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 40px;
}

.options--button {
    margin-top: 40px;
    width: 80%;
    height: 60px;
    border: none;
    color: var(--color-grey-dark);
    background-image: var(--color-gradient);
    border-radius: 10px;
}

.options--button:hover {
    cursor: pointer;
}

.options--button:active {
    background-image: none;
    background-color: var(--color-red);
}
/*#endregion*/

/*#region OptionsElement and OptionsSetElement*/
.optionsElement--div, .optionsSetElement--div {
    margin-top: 40px;
    padding-bottom: 10px;
    border-radius: 8px;
    box-shadow: 6px 6px 0px 6px var(--color-grey-light);
}

.optionsElement--div--title, .optionsSetElement--div--title {
    margin-bottom: 5px;
}

.optionsElement--div--select, .optionsSetElement--div--select {
    display: inline;
    vertical-align: middle;
}

.optionsElement--div--value {
    display: inline-block;
    align-items: center;
    vertical-align: middle;
    width: 60px;
    border: 2px solid var(--color-grey-dark);
    border-radius: 5px;
    margin-left: 10px;
    margin-right: 10px;
    pointer-events: none;
}

.optionsSetElement--div--value {
    display: inline-block;
    align-items: center;
    vertical-align: middle;
    border: 2px solid var(--color-grey-dark);
    border-radius: 5px;
    margin-left: 10px;
    margin-right: 10px;
    padding-left: 10px;
    padding-right: 10px;
    pointer-events: none;
}

.arrow {
    width: 35px;
    height: 35px;
    color: var(--color-orange);
    vertical-align: middle;
}

.arrow:hover {
    cursor: pointer;
}

.arrow:active {
    color: var(--color-red);
}

.arrow--disabled {
    width: 35px;
    height: 35px;
    color: var(--color-grey-light);
    opacity: 0.5;
    vertical-align: middle;
}
/*#endregion*/

/*#region Sets*/
.sets--div {
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 40px;
}

.sets--add--button {
    margin-top: 20px;
    width: 100%;
    height: 60px;
    border: none;
    color: var(--color-grey-dark);
    background-image: var(--color-gradient);
    border-radius: 10px;
    display: flex;
    align-items: center;
}

.sets--add--button:hover {
    cursor: pointer;
}

.sets--add--button:active {
    background-image: none;
    background-color: var(--color-red);
}

.sets--add--button--text {
    margin: auto ;
}

.sets--add--button--icon {
    color: var(--color-grey-dark);
    width: 30px;
    height: 30px;
    margin: auto;
}

.sets--view--div {
    margin-right: auto;
    padding-right: 20px;
    margin-top: 40px;
    overflow-y: auto;
    height: 500px;
}

.sets--view--div::-webkit-scrollbar {
    display: none;
    width: 0px;
}

.sets--view--element {
    margin-bottom: 40px;
    padding-bottom: 10px;
    border-radius: 8px;
    box-shadow: 6px 6px 0px 6px var(--color-grey-light);
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    position: relative;
}

.sets--view--element:hover {
    cursor: pointer;
}

.sets--view--element > .arrow {
    position: absolute;
    right: 2%;
    top: 15%;
    z-index: 2;
}

.sets--view--element > .arrow:hover {
    color: var(--color-red);
}

.sets--view--element--text {
    vertical-align: middle;
    align-items: center;
}

.checkmark {
    width: 35px;
    height: 35px;
    color: green;
    position: absolute;
    left: 2%;
    top: 15%;
}
/*#endregion*/

/*#region SetsElement*/
.sets--element--title {
    margin-top: 40px;
    padding-bottom: 10px;
    border-radius: 8px;
    box-shadow: 6px 6px 0px 6px var(--color-grey-light);
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.sets--element--arrow {
    width: 35px;
    height: 35px;
    color: var(--color-orange);
    margin-left: 5px;
    margin-right: 5px;
    margin-top: auto;
}

.sets--element--arrow:hover {
    cursor: pointer;
    color: var(--color-red);
}

.sets--element--delete {
    width: 30px;
    height: 30px;
    color: var(--color-orange);
    margin-right: 5px;
    margin-left: 5px;
}

.sets--element--delete:hover {
    cursor: pointer;
    color: var(--color-red);
}

.sets--element--title--text {
    margin-left: 10px;
    margin-right: auto;
    margin-top: auto;

}

.sets--element--title--form {
    margin: 0;
    padding: 0;
}

.sets--element--title--input {
    margin-top: 0;
    margin-bottom: 0;
    height: 100%;
    width: 70%;
    background-color: var(--color-white);
    border: none;
    font-size: 30px;
    font-weight: bold;
    text-align: center;
}

.sets--element--title--input::placeholder {
    color: var(--color-grey-dark);
}
/*#endregion*/

/*#region SetsElementList*/
.sets--element--list {
    margin-right: auto;
    padding-right: 20px;
    margin-top: 40px;
    overflow-y: auto;
    height: 400px;
    width: 100%;
}

.sets--element--list::-webkit-scrollbar {
    visibility: hidden;
    width: 0px;
}

.sets--element--list--element {
    padding-bottom: 10px;
}

.sets--element--list--input {
    margin-top: 0;
    margin-bottom: 0;
    background-color: var(--color-white);
    border: none;
    color: var(--color-grey-light);
    font-size: 25px;
    font-weight: 500;
    margin-bottom: 5px;
    width: 80%;
}

.sets--element--list--delete {
    width: 24px;
    height: 24px;
    color: var(--color-grey-light);
}

.sets--element--list--delete:hover {
    cursor: pointer;
    color: var(--color-orange);
}

.sets--element--list--add {
    width: 50px;
    height: 50px;
    color: var(--color-orange)
}

.sets--element--list--add:hover {
    cursor: pointer;
    color: var(--color-red);
}
/*#endregion*/

/*#region Game*/
.game--div {
    margin-top: 40px;
    margin-bottom: 40px;
    padding-bottom: 10px;
    border: 1px solid var(--color-grey-light);
    border-radius: 8px;
    box-shadow: 6px 6px 0px 6px var(--color-grey-light);
    width: 90%;
    height: 60%;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.game--div:hover {
    cursor: pointer;
}

.game--div > h1 {
    color: var(--color-orange);
}

.game--div > h2 {
    margin-top: 20px;
    color: var(--color-grey-light);
}

.game--role--h1 {
    font-size: 60px;
}

.game--role--h2 {
    text-align: justify;
    text-justify: inter-word;
    margin-left: 10px;
    margin-right: 10px;
}

.game--role--button {
    margin-top: 40px;
    width: 90%;
    height: 60px;
    border: none;
    color: var(--color-grey-dark);
    background-image: var(--color-gradient);
    border-radius: 10px;
}

.game--role--button:hover {
    cursor: pointer;
}

.game--role--button:active {
    background-image: none;
    background-color: var(--color-red);
}
/*#endregion*/